import React, { useState } from 'react';
import {
  Container,
  Row,
  Col,
  Card,
  Button,
  ProgressBar,
  Form
} from 'react-bootstrap';
import { Radar } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  RadarController,
  PointElement,
  CategoryScale,
  LinearScale,
  Tooltip,
  Legend,
  RadialLinearScale,
  LineElement,
  LineController,
  
} from 'chart.js';

ChartJS.register(
  RadarController,
  PointElement,
  CategoryScale,
  LinearScale,
  Tooltip,
  Legend,
  RadialLinearScale,
  LineElement,
  LineController
);

interface GrowthArea {
  id: number;
  name: string;
  goal: string;
  behavior: string;
  progress: number;
  keyword: string;
  lastUpdate: string;
}

interface CheckIn {
  area: string;
  date: string;
  note: string;
}

interface MonthlyTheme {
  month: string;
  theme: string;
  prompt: string;
  icon: string;
  color: string;
}

const GrowthTracking: React.FC = () => {
  const [checkIns] = useState<CheckIn[]>([
    { area: '', date: '2025-06-26', note: '' },
    { area: '', date: '2025-06-25', note: '' }
  ]);

  const growthAreas: GrowthArea[] = [
    {
      id: 1,
      name: '',
      goal: '',
      behavior: '1',
      progress: 65,
      keyword: '',
      lastUpdate: '2025-06-24'
    },
    {
      id: 2,
      name: '',
      goal: '',
      behavior: '',
      progress: 45,
      keyword: '',
      lastUpdate: '2025-06-25'
    }
  ];

  const monthlyTheme: MonthlyTheme = {
    month: '2025-06',
    theme: '',
    prompt: '',
    icon: '',
    color: '#D3E4CD'
  };

  const radarData = {
    labels: growthAreas.map(a => a.name),
    datasets: [
      {
        label: '',
        data: growthAreas.map(a => a.progress),
        backgroundColor: 'rgba(54, 162, 235, 0.2)',
        borderColor: 'rgba(54, 162, 235, 1)',
        borderWidth: 1
      }
    ]
  };

  return (
    <Container className="py-5">
      <h1 className="mb-4"></h1>

      {/*  */}
      <Card className="mb-4" style={{ backgroundColor: monthlyTheme.color }}>
        <Card.Body>
          <h4>{monthlyTheme.icon} {monthlyTheme.theme}</h4>
          <p>{monthlyTheme.prompt}</p>
        </Card.Body>
      </Card>

      <Row>
        <Col md={8}>
          {/*  +  */}
          <Card className="mb-4">
            <Card.Body>
              <h3> + </h3>
              {growthAreas.map(area => (
                <div key={area.id} className="mb-4 p-3 bg-light rounded">
                  <div className="d-flex justify-content-between align-items-center">
                    <h5>{area.name}</h5>
                    <small className="text-muted">{area.keyword}</small>
                  </div>
                  <p><strong></strong>{area.goal}</p>
                  <p><strong></strong>{area.behavior}</p>
                  <ProgressBar now={area.progress} label={`${area.progress}%`} className="mb-2" />
                  <div className="d-flex justify-content-between">
                    <small>: {area.lastUpdate}</small>
                    <Button variant="outline-secondary" size="sm"></Button>
                  </div>
                </div>
              ))}
              <div className="text-center mt-3">
                <Button variant="primary"> </Button>
              </div>
            </Card.Body>
          </Card>

          {/*  */}
          <Card>
            <Card.Body>
              <h3></h3>
              <Radar data={radarData} />
            </Card.Body>
          </Card>
        </Col>

        <Col md={4}>
          {/*  */}
          <Card className="mb-4">
            <Card.Body style={{ padding: '1rem' }}>
              <h4> </h4>
              {checkIns.map((c, i) => (
                <div key={i} className="mb-2 bg-light p-2 rounded">
                  <small className="text-muted">{c.date}  {c.area}</small>
                  <p className="mb-0">{c.note}</p>
                </div>
              ))}
              <div className="text-center mt-2">
                <Button variant="outline-primary" size="sm"></Button>
              </div>
            </Card.Body>
          </Card>

          {/*  */}
          <Card>
            <Card.Body>
              <h4> </h4>
              <Form.Control as="textarea" placeholder=" " rows={3} className="mb-2" />
              <div className="text-end">
                <Button variant="outline-secondary" size="sm"></Button>
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  );
};

export default GrowthTracking;
